
import { useStore } from "vuex";
export default {
  emits: ["search"],
  setup() {
    const alphabet = "abcdefghijklmnopqrstuvwxyz".toUpperCase().split("");
    const store = useStore();
    function requestAPI(letter: string) {
      store.commit("searchInApi", letter);
    }
    return {
      alphabet,
      requestAPI,
    };
  },
};
